export default {
	data() {
		return {
			piclist :[],    //相册图片数组
			imgnumber : '', //图片张数
			scSpotContent : {}, //景区内容
			scrollTop: 0,	//相册图片
		}
	},
	onLoad(){
		// let id = options.id;
		// if(id){
		// 	this.$api.msg(`点击了${id}`);
		// }
		this.lyfwData();
		
	},
	onNavigationBarButtonTap : function() {
				 this.share();
	},
	onShareAppMessage(res){
		  if (res.from === 'menu') {// 来自页面内分享按钮
		     return{
		     	title : '来自'+this.userInfo.nickname+'的分享',
				imageUrl : this.scSpotContent.image[0].src,
				success: function () {
				    uni.showToast({
				    	title:'分享成功',
						duration : 3000
				    })
				},
				fail: function () {
				    uni.showToast({
				    	title:'分享失败',
						duration : 3000
				    })
				}
		     }
		    }
			
	},
	methods: {
		//读取静态数据json.js 
		async lyfwData(){
			let scSpotDetails = await this.$api.lyfw('scSpotDetails');
			this.scSpotContent = scSpotDetails;
			this.piclist = scSpotDetails.image;
			this.imgnumber = scSpotDetails.imageNumber;
			// console.log(this.scSpotContent)
		},
		//保存图片至本地并打开新页面
		goImgList(){
			uni.setStorageSync('imagePiclist',this.piclist);
			uni.navigateTo({
				url:'/pages/LYFW/currency/imglist'
			})
		},
		//路由整合
		godetail : function (url){
			// console.log(id)
			// uni.showToast({
			// 	title:'你点击了'+value,
			// 	icon: 'none'
			// })
			uni.navigateTo({
				url
			})
		},
		
		//分享
		share(){
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 0,
			    href: "http://www.baidu.com",
			    title: "来自"+this.userInfo.nickname+"的分享",
			    summary: this.scSpotContent.title,
			    imageUrl: this.scSpotContent.image[0].src,
			    success: function () {
			        uni.showToast({
			        	title:'分享成功',
						duration : 3000
			        })
			    },
			    fail: function () {
			        uni.showToast({
			        	title:'分享失败',
						duration : 3000
			        })
			    }
			}); 
		},
		// 统一跳转接口
		navTo(url){
			uni.navigateTo({
				url
			})  
		},
		
	} 
}
